<template>
  <div>
    <!--mainwrap-->
    <div class="row">
      <div class="col-md-12">
        <div class="card card-custom gutter-b">
          <div class="card-header">
            <div class="card-title">
              <h3 class="card-label">
                Sinyal Huobi Dodo HECO
                <button @click="kalkulatorTool('USDT', 0)" class="btn btn-info ml-2"><b-icon-calculator-fill /> Kalkulator</button>
              </h3>
              <b-form-checkbox switch v-model="auto_request">Autorun</b-form-checkbox>
            </div>
            <div class="card-toolbar">
              <span class="mr-2" v-if="axiosError.timeout.length > 0">
                <span style="color: brown; font-weight: bold; font-size: 14px" :title="axiosError.timeout.join('\n')"><b-icon-clock /> {{ axiosError.timeout.length }} Timeout</span> |
              </span>
              <span class="mr-2" v-if="axiosError.error.length > 0">
                <span style="color: #ff0000; font-weight: bold; font-size: 14px" :title="axiosError.error.join('\n')"><b-icon-exclamation-triangle /> {{ axiosError.error.length }} Error</span> |
              </span>
              <span class="mr-2" v-if="threshold > 0">
                <span style="color: #00aa00; font-weight: bold; font-size: 14px"><b-icon-bell /> {{ threshold }} Sinyal</span>
              </span>
              <span v-if="running_request > 0 || request_run" class="text-muted"
                ><b-spinner small /> Update Harga Koin {{ (((total_request - running_request) / total_request) * 100).toFixed(1) }}%
              </span>
              <button v-else :disabled="sinyal.fetching" @click="startFetchSinyalHuobiDodoHeco()" class="btn btn-success" style="background: #00dd00" title="Klik refresh untuk memperbarui data">
                <b-icon-clock /> Update
              </button>
              <button v-if="running_request > 0 || request_run" @click="$router.go()" class="btn btn-danger ml-2">Stop/Refresh</button>
              <b-form-select :disabled="running_request > 0 || request_run" v-model="stableCoin" :options="opsiStableCoin" class="ml-1" style="width:100px;"></b-form-select>
              <button @click="openModalLog" class="btn btn-danger ml-2"><b-icon-clock-history /> Log</button>
              <!-- <button v-if="running_request > 0" @click="cancelFetch()" class="btn btn-primary ml-1" title="Klik untuk membatalkan request saat ini"><b-icon-x-square-fill /> Cancel</button> -->
            </div>
          </div>
          <div class="card-body">
            <b-row>
              <b-col md="12" class="mb-5">
                <table class="table table-bordered table-sm">
                  <thead>
                    <tr>
                      <th class="text-center p-1" style="background-color: #f0f8ff">BTC/USDT</th>
                      <th class="text-center p-1" style="background-color: #ffffff">HT/USDT</th>
                      <th class="text-center p-1" style="background-color: #f0f8ff">ETH/USDT</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td class="text-center font-weight-bolder p-1" style="background-color: #f0f8ff">{{ harga.btcusdt }}</td>
                      <td class="text-center font-weight-bolder p-1" style="background-color: #ffffff">{{ harga.htusdt }}</td>
                      <td class="text-center font-weight-bolder p-1" style="background-color: #f0f8ff">{{ harga.ethusdt }}</td>
                    </tr>
                  </tbody>
                </table>
              </b-col>
            </b-row>
            <b-row>
              <b-col md="3">
                <b-form-group id="modal_kiri_huobi_dodo_heco" label="Modal Huobi:" label-cols-sm="5" label-align-sm="right" label-for="modal_kiri_huobi_dodo_heco">
                  <b-input-group>
                    <b-form-input id="modal_kiri_huobi_dodo_heco" type="number" placeholder="Max" title="Max" :value="modal_kiri_huobi_dodo_heco" @input="updateModalKiri" required></b-form-input>
                  </b-input-group>
                </b-form-group>
              </b-col>
              <b-col md="3">
                <b-form-group id="modal_kanan_huobi_dodo_heco" label="Modal Dodo:" label-cols-sm="5" label-align-sm="right" label-for="modal_kanan_huobi_dodo_heco">
                  <b-input-group>
                    <b-form-input id="modal_kanan_huobi_dodo_heco" type="number" placeholder="Max" title="Max" :value="modal_kanan_huobi_dodo_heco" @input="updateModalKanan" required></b-form-input>
                  </b-input-group>
                </b-form-group>
              </b-col>
              <b-col md="1">
                <button
                  v-if="!(running_request > 0 || request_run)"
                  :disabled="sinyal.fetching"
                  @click="registerAndStartFetchSinyalHuobiDodoHeco()"
                  class="btn btn-primary mr-1"
                  title="Klik reset untuk menerapkan perubahan modal ke semua coin"
                >
                  Apply
                </button>
              </b-col>
              <b-col md="3">
                <b-form-group id="limit_threshold" label="Filter PNL:" label-cols-sm="5" label-align-sm="right" label-for="limit_threshold">
                  <b-input-group>
                    <b-form-input id="limit_threshold" type="number" placeholder="Filter PNL" v-model="limit_threshold" required></b-form-input>
                    <b-input-group-append is-text title="Sembunyikan yg tidak masuk kriteria">
                      <b-form-checkbox switch class="mr-n2" v-model="sinyal.table_filter" value="y" unchecked-value="" />
                    </b-input-group-append>
                  </b-input-group>
                </b-form-group>
              </b-col>
              <b-col md="2">
                <b-form-group id="kontrol_nonaktif" label="Aktif" label-cols-sm="4" label-align-sm="right" label-for="kontrol_nonaktif">
                  <b-input-group>
                    <b-form-input disabled id="kontrol_nonaktif" type="text" :value="jml_koin - list_koin_nonaktif.length + '/' + jml_koin" required></b-form-input>
                    <b-input-group-append is-text title="Sembunyikan yg tidak masuk kriteria">
                      <b-form-checkbox switch class="mr-n2" v-model="filter_nonaktif" value="y" unchecked-value="" title="Klik untuk menyembunyikan koin nonaktif" />
                    </b-input-group-append>
                  </b-input-group>
                </b-form-group>
              </b-col>
            </b-row>
            <b-table
              bordered
              sticky-header="900px"
              head-variant="dark"
              responsive=""
              hover
              :items="listSinyalHuobiDodoHeco"
              :fields="sinyal.fields"
              :tbody-tr-class="rowClass"
              :filter-function="filterTable"
              :filter="filter_tabel"
            >
              <template slot="thead-top" style="position: sticky">
                <b-tr>
                  <b-th>Modal</b-th>
                  <b-th colspan="2">PRICE</b-th>
                  <b-th colspan="2" variant="primary">SELISIH</b-th>
                  <b-th class="text-center">SYMBOL</b-th>
                  <b-th>Modal</b-th>
                  <b-th colspan="2">PRICE</b-th>
                  <b-th colspan="2" variant="primary">SELISIH</b-th>
                  <b-th style="padding: 0px;text-align: center;vertical-align: middle;font-size:18px;color:#f00;" :title="list_koin_nonaktif.map(e => e.nama_coin).join('\n')">
                    <b-form-checkbox :checked="aktifAll" switch v-on:change="ubahAktifAll(aktifAll)" />
                  </b-th>
                </b-tr>
              </template>
              <template #cell(symbol)="data">
                <span @click="bintangMark($event, data.value)" :class="'fa fa-star mr-2 ' + bintangFind(data.value)"></span>
                <a :href="listSettings.url_coin + data.item.coin_address" target="_pasar">{{ data.value }}</a>
                <span :class="'ml-2 badge badge-pill badge-pair-' + data.item.pair">{{ data.item.pair }}</span>
                {{ data.item.waktu_fetch }}
                <a target="wallet" :href="'https://hecoscan.com/token/' + data.item.coin_address + '?a=0x4982085c9e2f89f2ecb8131eca71afad896e89cb&q='">Stok</a>
                <span style="color:red;margin-left:4px;">{{ data.item.offset_pnl == 0 ? "" : "-" + data.item.offset_pnl + "%" }}</span>
                <br />
                <small style="cursor: copy" :id="'coin_address_' + data.item.symbol" @click="CopyToClipboard('coin_address_' + data.item.symbol)">{{ data.item.coin_address }}</small>
              </template>
              <template #cell(huobi_ask_price)="data">
                <a
                  target="koin"
                  :href="'https://www.huobi.com/en-us/exchange/' + data.item.symbol.toLowerCase() + '_' + data.item.pair.toLowerCase() + '?type=spot'"
                  :title="data.item.ask_xprice !== null ? data.item.ask_xprice + ' ' + data.item.pair : ''"
                >
                  <span :id="'huobi_ask_price_' + data.item.symbol" v-html="parseFloat(data.value).toFixed(12)"></span>
                </a>
                <br />
                <a class="price-tool mr-2" style="cursor: copy" title="Klik untuk copy" @click="CopyToClipboard('huobi_ask_price_' + data.item.symbol)"><b-icon-clipboard-plus /></a>
                <a class="price-tool" title="Klik untuk mengkalkulasi" @click="kalkulatorTool(data.item.pair, parseFloat(data.value).toFixed(8))"><b-icon-calculator /></a>
              </template>
              <template #cell(huobi_bid_price)="data">
                <a
                  target="koin"
                  :href="'https://www.huobi.com/en-us/exchange/' + data.item.symbol.toLowerCase() + '_' + data.item.pair.toLowerCase() + '?type=spot'"
                  :title="data.item.bid_xprice !== null ? data.item.bid_xprice + ' ' + data.item.pair : ''"
                >
                  <span :id="'huobi_bid_price_' + data.item.symbol" v-html="parseFloat(data.value).toFixed(12)"></span>
                </a>
                <br />
                <a class="price-tool mr-2" style="cursor: copy" title="Klik untuk copy" @click="CopyToClipboard('huobi_bid_price_' + data.item.symbol)"><b-icon-clipboard-plus /></a>
                <a class="price-tool" title="Klik untuk mengkalkulasi" @click="kalkulatorTool(data.item.pair, parseFloat(data.value).toFixed(8))"><b-icon-calculator /></a>
              </template>
              <template #cell(inch_usdt)="data">
                <a
                  target="koin2"
                  :href="'https://app.dodoex.io/?network=heco&from=' + data.item.coin_address + '&to=0x0298c2b32eae4da002a15f36fdf7615bea3da047'"
                  :title="data.item.ask_xprice !== null ? (parseFloat(data.value) / parseFloat(data.item.ask_pairPrice)).toFixed(8) + ' ' + data.item.pair : ''"
                >
                  <span :id="'inch_usdt' + data.item.symbol" v-html="data.value"></span>
                </a>
                <br />
                <a class="price-tool mr-2" style="cursor: copy" title="Klik untuk copy" @click="CopyToClipboard('inch_usdt' + data.item.symbol)"><b-icon-clipboard-plus /></a>
                <a class="price-tool" title="Klik untuk mengkalkulasi" @click="kalkulatorTool(data.item.pair, parseFloat(data.value).toFixed(8))"><b-icon-calculator /></a>
              </template>
              <template #cell(selisih_pnl_kiri)="data">
                <span v-bind:class="{ 'bg-primary text-white p-2': parseFloat(data.value) >= parseFloat(limit_threshold) }" v-html="data.value"></span>
              </template>
              <template #cell(selisih_pnl_kiri_persen)="data" class="text-center">
                <span v-html="data.value"></span>
              </template>
              <template #cell(inch_price)="data">
                <a
                  target="koin2"
                  :href="'https://app.dodoex.io/?network=heco&from=0x0298c2b32eae4da002a15f36fdf7615bea3da047&to=' + data.item.coin_address"
                  :title="data.item.bid_xprice !== null ? (parseFloat(data.value) / parseFloat(data.item.bid_pairPrice)).toFixed(8) + ' ' + data.item.pair : ''"
                >
                  <span :id="'inch_price' + data.item.symbol" v-html="data.value"></span>
                </a>
                <br />
                <a class="price-tool mr-2" style="cursor: copy" title="Klik untuk copy" @click="CopyToClipboard('inch_price' + data.item.symbol)"><b-icon-clipboard-plus /></a>
                <a class="price-tool" title="Klik untuk mengkalkulasi" @click="kalkulatorTool(data.item.pair, parseFloat(data.value).toFixed(8))"><b-icon-calculator /></a>
              </template>
              <!-- <template #cell(inch_sum)="data">
                <span v-html="data.value"></span>
              </template> -->
              <template #cell(selisih_pnl_kanan)="data">
                <span v-bind:class="{ 'bg-primary text-white p-2': parseFloat(data.value) >= parseFloat(limit_threshold) }" v-html="data.value"></span>
              </template>
              <template #cell(selisih_pnl_kanan_persen)="data">
                <span v-html="data.value"></span>
              </template>
              <template #cell(modal_kiri_huobi_dodo_heco)="data">
                <input class="form-control" type="number" v-on:change="indvModal($event, data.item.symbol, 'kiri')" :value="data.value" title="Ketikkan modal, tekan enter untuk memuat." />
              </template>
              <template #cell(modal_kanan_huobi_dodo_heco)="data">
                <input class="form-control" type="number" v-on:change="indvModal($event, data.item.symbol, 'kanan')" :value="data.value" title="Ketikkan modal, tekan enter untuk memuat." />
              </template>
              <template #cell(num)="data">
                <span v-html="data.index + 1"></span>
                <b-form-checkbox switch :checked="data.item.aktif" v-on:change="ubahAktif(data.item.symbol, data.item.aktif)" />
              </template>
            </b-table>
          </div>
          <div class="card-footer">
            <b-row>
              <b-col md="12">
                <h4>Pengaturan update harga :</h4>
              </b-col>
            </b-row>
            <b-row>
              <b-col md="4">
                <b-form-group id="axiosDelay" label="Interval" label-cols-sm="6" label-align-sm="right" label-for="axiosDelay">
                  <b-input-group>
                    <b-form-input id="axiosDelay" type="number" placeholder="Delay" :value="axiosDelay" @input="updateDelay" required></b-form-input>
                    <b-input-group-append is-text title="">ms / koin</b-input-group-append>
                  </b-input-group>
                </b-form-group>
              </b-col>
              <b-col md="3">
                <b-form-group id="auto_request_delay_huobi_dodo_heco" label="Auto Delay" label-cols-sm="6" label-align-sm="right" label-for="auto_request_delay_huobi_dodo_heco">
                  <b-input-group>
                    <b-form-input
                      id="auto_request_delay_huobi_dodo_heco"
                      type="number"
                      placeholder="Delay"
                      :value="auto_request_delay_huobi_dodo_heco"
                      @input="updateAutoRequestDelay"
                      required
                    ></b-form-input>
                    <b-input-group-append is-text title="Milidetik">ms</b-input-group-append>
                  </b-input-group>
                </b-form-group>
              </b-col>
              <b-col md="5">
                <b-form-group id="axiosBatch" label="Jeda:" label-cols-sm="3" label-align-sm="right" label-for="axiosBatch">
                  <b-input-group>
                    <b-form-input id="axiosRehat" type="number" placeholder="Rehat" :value="axiosRehat" @input="updateRehat" required></b-form-input>
                    <b-input-group-append is-text title="">ms per-</b-input-group-append>
                    <b-form-input id="axiosBatch" type="number" placeholder="Rehat" :value="axiosBatch" @input="updateBatch" required></b-form-input>
                    <b-input-group-append is-text title="">koin</b-input-group-append>
                  </b-input-group>
                </b-form-group>
              </b-col>
            </b-row>
            <!-- <b-row>
              <b-col md="8">
                <b-form-group id="protocol_Dodo" label="LP Dodo" label-cols-sm="2" label-align-sm="right" label-for="protocol_Dodo">
                  <b-form-input id="protocol_Dodo" type="text" placeholder="LP Dodo" :value="protocol_Dodo" @input="updateProtocol_Dodo" required></b-form-input>
                </b-form-group>
              </b-col>
            </b-row> -->
          </div>
        </div>
      </div>
    </div>
    <!-- modals -->
    <b-modal ref="modal-log" title="Log Request" size="xl">
      <div style="height: 400px; overflow: auto">
        <ul>
          <li v-for="(item, index) in log_request" :key="index">{{ item }}</li>
        </ul>
      </div>
    </b-modal>
    <b-modal ref="modal-calculator" title="Kalkulator" size="md" ok-only ok-title="Tutup">
      <kalkulator :pair="kalkulator_data.pair" :harga="kalkulator_data.harga" :trigger="kalkulator_data.trigger" />
    </b-modal>
  </div>
  <!--mainwrap-->
</template>

<script>
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";
import { mapGetters } from "vuex";
import axios from "axios";
import Swal from "sweetalert2";
import Kalkulator from "../../components/Kalkulator.vue";

export default {
  data() {
    return {
      sinyal: {
        fetching: false,
        totalRows: 1,
        currentPage: 1,
        perPage: 200,
        filter: null,
        filterOn: ["symbol"],
        table_filter: "",
        // Note 'isActive' is left out and will not appear in the rendered table
        fields: [
          {
            key: "modal_kiri_huobi_dodo_heco",
            label: "Huobi",
            sortable: false,
            tdClass: "text-center font-weight-bold",
            thStyle: {
              width: "10%",
            },
          },
          {
            key: "huobi_ask_price",
            label: "Huobi(Ask)",
            sortable: false,
            tdClass: "text-center font-weight-bold bg-price-huobi",
          },
          {
            key: "inch_usdt",
            label: "Dodo",
            sortable: false,
            tdClass: "text-center font-weight-bold bg-price-dodo",
          },
          {
            key: "selisih_pnl_kiri",
            label: "PNL",
            sortable: false,
            tdClass: "text-center font-weight-bold",
          },
          {
            key: "selisih_pnl_kiri_persen",
            label: "%",
            sortable: false,
            tdClass: "text-center font-weight-bold",
          },
          {
            key: "symbol",
            label: "Coin",
            sortable: true,
            tdClass: "text-center font-weight-bold bg-grey-100 text-dinamis bg-puteh relpos",
            thClass: "text-center",
            thStyle: {
              width: "40%",
            },
          },
          {
            key: "modal_kanan_huobi_dodo_heco",
            label: "Dodo",
            sortable: false,
            tdClass: "text-center font-weight-bold",
            thStyle: {
              width: "10%",
            },
          },
          {
            key: "inch_price",
            label: "Dodo",
            sortable: false,
            tdClass: "text-center font-weight-bold bg-price-dodo",
          },
          {
            key: "huobi_bid_price",
            label: "Huobi(Bid)",
            sortable: false,
            tdClass: "text-center font-weight-bold bg-price-huobi",
          },
          // {
          //   key: "inch_sum",
          //   label: "SUM",
          //   sortable: false,
          //   tdClass: "text-center font-weight-bold",
          // },
          {
            key: "selisih_pnl_kanan",
            label: "PNL",
            sortable: false,
            tdClass: "text-center font-weight-bold",
          },
          {
            key: "selisih_pnl_kanan_persen",
            label: "%",
            sortable: false,
            tdClass: "text-center font-weight-bold",
          },
          {
            key: "num",
            label: "#",
            sortable: false,
            tdClass: "text-center font-weight-bold bg-ireng text-white",
          },
        ],
        items: [],
      },
      modal_kiri_huobi_dodo_heco: 0,
      modal_kanan_huobi_dodo_heco: 0,
      running_request: 0,
      total_request: 0,
      auto_request: false,
      auto_request_delay_huobi_dodo_heco: 5000,
      request_run: false,
      threshold: 0,
      limit_threshold: 3,
      log_request: [],
      axiosCancelToken: axios.CancelToken,
      axiosCancel: null,
      axiosDelay: 500,
      axiosRehat: 10000,
      axiosBatch: 25,
      axiosError: {
        timeout: [],
        error: [],
      },
      harga: {
        btcusdt: 0,
        btcbidr: 0,
        ethusdt: 0,
        ethbidr: 0,
        htusdt: 0,
        bnbbidr: 0,
        usdtbidr: 0,
      },
      kalkulator_data: {
        pair: "USDT",
        harga: 0,
        trigger: true,
      },
      testing: "",
      jml_koin_aktif: 0,
      jml_koin: 0,
      list_koin_nonaktif: [],
      list_koin_bintang_huobi_dodo_heco: [],
      stableCoin: "0xe9e7cea3dedca5984780bafc599bd69add087d56",
      opsiStableCoin: [
        { text: "BUSD", value: "0xe9e7cea3dedca5984780bafc599bd69add087d56" },
        { text: "USDT", value: "0x55d398326f99059ff775485246999027b3197955" },
      ],
      skip_iteration: 0,
      aktifAll: true,
      filter_nonaktif: false,
    };
  },
  components: { Kalkulator },
  watch: {
    limit_threshold() {
      this.$store.dispatch("settingSet", { name: "filter_pnl_huobi_dodo_heco", value: this.limit_threshold });
      this.recalculateThreshold();
    },
    running_request(newVal, oldVal) {
      if (newVal == 0 && oldVal != 0) {
        if (this.auto_request) {
          this.request_run = true;
          this.$bvToast.toast("Request selesai, me-refresh dalam " + this.auto_request_delay_huobi_dodo_heco + "ms", {
            title: `Autorun`,
            variant: "success",
            solid: true,
          });
          setTimeout(() => {
            this.startFetchSinyalHuobiDodoHeco();
          }, this.auto_request_delay_huobi_dodo_heco);
        } else {
          this.request_run = false;
        }
      }
    },
  },
  computed: {
    ...mapGetters(["listSinyalHuobiDodoHeco", "lastFetchSinyalHuobiDodoHeco", "listSettings", "listHuobiBookTicker", "currentUser", "getLimitDodo"]),
    filter_tabel: function() {
      return [this.filter_nonaktif, this.sinyal.table_filter];
    },
  },
  mounted() {
    if (!(this.currentUser.level_akses == 1 || this.currentUser.fitur.find(i => i == "sinyal_huobi_dodo_heco") != undefined)) {
      this.$router.push({ name: "setting" });
    }
    this.modal_kiri_huobi_dodo_heco = this.listSettings === null ? 400 : this.listSettings.modal_kiri_huobi_dodo_heco || 400;
    this.modal_kanan_huobi_dodo_heco = this.listSettings === null ? 400 : this.listSettings.modal_kanan_huobi_dodo_heco || 400;
    this.limit_threshold = this.listSettings === null ? 5 : this.listSettings.filter_pnl_huobi_dodo_heco || 5;
    this.axiosDelay = this.listSettings === null ? 500 : this.listSettings.delay_huobi_dodo_heco || 500;
    this.axiosRehat = this.listSettings === null ? 10000 : this.listSettings.rehat_huobi_dodo_heco || 10000;
    this.axiosBatch = this.listSettings === null ? 25 : this.listSettings.batch || 25;
    this.auto_request_delay_huobi_dodo_heco = this.listSettings === null ? 5000 : this.listSettings.auto_request_delay_huobi_dodo_heco || 5000;
    this.$store.dispatch(SET_BREADCRUMB, [{ title: "Market Watch HUOBI DODO HECO" }]);
    this.recalculateThreshold();

    //harga
    this.updateHarga();
    this.jml_koin = this.$store.state.coin.coins_huobi_heco.length;
    this.jml_koin_aktif = this.$store.state.coin.coins_huobi_heco.filter(x => x.aktif_dodo == true).length;
    this.list_koin_nonaktif = this.$store.state.coin.coins_huobi_heco.filter(x => x.aktif_dodo == false);

    try {
      let koin_bintang_huobi_dodo_heco = localStorage.getItem("mw_koin_bintang_huobi_dodo_heco");
      if (koin_bintang_huobi_dodo_heco == null) {
        throw null;
      }
      koin_bintang_huobi_dodo_heco = JSON.parse(koin_bintang_huobi_dodo_heco);
      this.list_koin_bintang_huobi_dodo_heco = koin_bintang_huobi_dodo_heco;
    } catch (e) {
      console.log("mw_koin_bintang_huobi_dodo_heco", e);
      localStorage.setItem("mw_koin_bintang_huobi_dodo_heco", JSON.stringify([]));
      this.list_koin_bintang_huobi_dodo_heco = [];
    }
  },
  methods: {
    ubahAktif(symbol, current) {
      this.$store.dispatch("updateSinyalHuobiDodoHeco", {
        symbol: symbol,
        value: {
          aktif: !current,
        },
      });
      this.$store.dispatch("updateAktifCoinHuobiHeco", {
        nama_coin: symbol,
        edit: false,
        changes: {
          aktif_dodo: !current,
        },
      });
      this.jml_koin_aktif = this.$store.state.coin.coins_huobi_heco.filter(x => x.aktif_dodo == true).length;
      this.list_koin_nonaktif = this.$store.state.coin.coins_huobi_heco.filter(x => x.aktif_dodo == false);
    },
    ubahAktifAll(current) {
      this.aktifAll = !current;
      this.$store.state.coin.coins_huobi_heco.forEach(x => {
        this.$store.dispatch("updateSinyalHuobiDodoHeco", {
          symbol: x.nama_coin,
          value: {
            aktif: this.aktifAll,
          },
        });
        this.$store.dispatch("updateAktifCoinHuobiHeco", {
          nama_coin: x.nama_coin,
          edit: false,
          changes: {
            aktif_dodo: this.aktifAll,
          },
        });
      });

      this.jml_koin_aktif = this.$store.state.coin.coins_huobi_heco.filter(x => x.aktif_dodo == true).length;
      this.list_koin_nonaktif = this.$store.state.coin.coins_huobi_heco.filter(x => x.aktif_dodo == false);
    },
    addLogRequest(text) {
      this.log_request.push("[" + new Date().toISOString() + "]" + text);
