    openModalLog() {
      this.$refs["modal-log"].show();
    },
    recalculateThreshold() {
      this.threshold = 0;
      this.listSinyalHuobiDodoHeco.forEach(item => {
        if (parseFloat(item.selisih_pnl_kanan) >= this.limit_threshold || parseFloat(item.selisih_pnl_kiri) >= this.limit_threshold) this.threshold++;
      });
    },
    getBidPrice(pair) {
      let data = this.listHuobiBookTicker.find(item => item.symbol == pair);
      console.log(pair, data);
      if (data == undefined) {
        return 0;
      } else {
        return data.bidPrice;
      }
