    filterTable(row, filter) {
      // console.log("filter", filter);
      if (filter[0] == "y" && !row.aktif) {
        return false;
      }
      if (filter[1] == "y") {
        const offset_pnl = row.offset_pnl === undefined ? 0 : parseFloat(row.offset_pnl);
        console.log(row.symbol, offset_pnl >= 0 && (row.selisih_pnl_kiri_persen > offset_pnl || row.selisih_pnl_kanan_persen > offset_pnl));
        if (
          row.aktif &&
          (parseFloat(row.selisih_pnl_kanan) >= this.limit_threshold || parseFloat(row.selisih_pnl_kiri) >= this.limit_threshold) &&
          (offset_pnl == 0 || (offset_pnl >= 0 && (row.selisih_pnl_kiri_persen > offset_pnl || row.selisih_pnl_kanan_persen > offset_pnl)))
        ) {
          return true;
        } else {
          return false;
        }
      } else {
        return true;
      }
    },
    indvModal(val, sym, k) {
