    cancelFetch() {
      window.location.reload(false);
      // this.axiosCancel.cancel();
    },
    startFetchSinyalHuobiDodoHeco() {
      // let pelanggaran = 0;
      // this.listSinyalHuobiDodoHeco.forEach(item => {
      //   if (item.modal_kiri_huobi_dodo_heco < this.getLimitDodo[0] || item.modal_kiri_huobi_dodo_heco > this.getLimitDodo[1]) {
      //     pelanggaran++;
      //   }
      //   if (item.modal_kanan_huobi_dodo_heco < this.getLimitDodo[0] || item.modal_kanan_huobi_dodo_heco > this.getLimitDodo[1]) {
      //     pelanggaran++;
      //   }
      // });
      // if (pelanggaran > 0) {
      //   Swal.fire({
      //     title: "Modal tidak valid!",
      //     text: "Modal anda harus diatas " + this.getLimitDodo[0] + " dan dibawah " + this.getLimitDodo[1],
      //     icon: "error",
      //     heightAuto: false,
      //   });
      //   return false;
      // }
      this.clearHarga();
      this.axiosCancel = this.axiosCancelToken.source();
      this.threshold = 0;
      this.total_request = 0;
      this.$store.dispatch("clearSinyalHuobiDodoHeco").then(() => {
        this.total_request++;
        this.$store.dispatch("updateHuobiBookTicker").then(response => {
          // console.log("respon", response);
          this.updateHarga();
          if (response.status != "ok") {
            Swal.fire({
              title: "Gagal memuat book ticker!",
              text: response.message,
              icon: "error",
              heightAuto: false,
            });
          } else {
            this.addLogRequest("HUOBI UPDATED");
            this.skip_iteration = 0;
            this.listSinyalHuobiDodoHeco.forEach((item, iteration) => {
              if (!item.aktif) {
                this.skip_iteration++;
              } else {
                this.fetchSinyalHuobiDodoHeco(item.symbol, undefined, iteration - this.skip_iteration);
              }
            }); // forEach
            this.$bvToast.toast("Book Ticker berhasil huobi di update", {
              title: `Huobi Book Ticker`,
              variant: "success",
              solid: true,
            });
          }
        });
      }); // dispatch clearSinyalHuobiDodoHeco
    },
    fetchSinyalHuobiDodoHeco(symbol, addition, iteration) {
      // let istirahat = iteration % 25 == 0 ? iteration * 10000 : 0;
      let rehat_huobi_dodo_heco = this.axiosRehat * Math.floor(iteration / this.axiosBatch);
      let penambah = this.axiosDelay * iteration;
      this.axiosError.timeout = [];
      this.axiosError.error = [];
      this.running_request += 2;
      this.total_request += 2;
      let sinyal = this.listSinyalHuobiDodoHeco.find(item => item.symbol == symbol);
      let coin = this.$store.state.coin.coins_huobi_heco.find(item => item.nama_coin == symbol);
      if (addition == undefined) {
        addition = {};
      }
      let askPrice = this.$store.getters.getHuobiBookCoinPriceAsk(symbol);
      let bidPrice = this.$store.getters.getHuobiBookCoinPriceBid(symbol);
      // if (iteration !== null) {
      //   setTimeout(() => {
      //     this.running_request -= 2;
      //     console.log("fetchdodo", rehat_huobi_dodo_heco + penambah, " : " + iteration + " : " + rehat_huobi_dodo_heco);
      //   }, rehat_huobi_dodo_heco + penambah);
      //   return true;
      // }
      setTimeout(() => {
        this.$store
          .dispatch("updateSinyalHuobiDodoHeco", {
            symbol: symbol,
            value: {
              ...addition,
              huobi_ask_price: askPrice.price,
              ask_xprice: askPrice.xprice !== undefined ? askPrice.xprice : null,
              huobi_bid_price: bidPrice.price,
              bid_xprice: bidPrice.xprice !== undefined ? bidPrice.xprice : null,
              ask_pairPrice: askPrice.pairPrice !== undefined ? askPrice.pairPrice : null,
              bid_pairPrice: bidPrice.pairPrice !== undefined ? bidPrice.pairPrice : null,
              selisih_pnl_kiri: "<div class='spinner-border spinner-border-sm'></div>",
              inch_usdt: "<div class='spinner-border spinner-border-sm'></div>",
              selisih_pnl_kiri_persen: "<div class='spinner-border spinner-border-sm'></div>",
              inch_sum: "<div class='spinner-border spinner-border-sm'></div>",
              inch_price: "<div class='spinner-border spinner-border-sm'></div>",
              selisih_pnl_kanan: "<div class='spinner-border spinner-border-sm'></div>",
              selisih_pnl_kanan_persen: "<div class='spinner-border spinner-border-sm'></div>",
              gasKiri: "?",
              gasKanan: "?",
            },
          })
          .then(() => {
            if (sinyal.huobi_ask_price == 0) {
              this.$store.dispatch("updateSinyalHuobiDodoHeco", {
                symbol: symbol,
                value: {
                  ...addition,
                  selisih_pnl_kiri: 0,
                  inch_usdt: 0,
                  selisih_pnl_kiri_persen: 0,
                  inch_sum: 0,
                  inch_price: 0,
                  selisih_pnl_kanan: 0,
                  selisih_pnl_kanan_persen: 0,
                },
              });
              this.running_request -= 2;
              return true;
            }
            let modal_kiri_huobi_dodo_heco = addition.modal_kiri_huobi_dodo_heco != undefined ? addition.modal_kiri_huobi_dodo_heco : sinyal.modal_kiri_huobi_dodo_heco;
            let modal_kanan_huobi_dodo_heco = addition.modal_kanan_huobi_dodo_heco != undefined ? addition.modal_kanan_huobi_dodo_heco : sinyal.modal_kanan_huobi_dodo_heco;
            // console.log(askPrice);
            // console.log(((modal_kiri_huobi_dodo_heco / askPrice) * Math.pow(10, parseInt(coin.desimal))).toLocaleString("fullwide", { useGrouping: false }));
            // console.log(parseInt((modal_kiri_huobi_dodo_heco / askPrice) * Math.pow(10, parseInt(coin.desimal))));
            // let amount = BigInt(parseInt((modal_kiri_huobi_dodo_heco / askPrice) * Math.pow(10, parseInt(coin.desimal)))).toString();
            // console.log(amount);
            let amount = ((modal_kiri_huobi_dodo_heco / askPrice.price) * Math.pow(10, parseInt(coin.desimal))).toLocaleString("fullwide", { useGrouping: false, maximumFractionDigits: 0 });
            axios
              .get("https://route-api.dodoex.io/dodoapi/getdodoroute", {
                cancelToken: this.axiosCancel.token,
                params: {
                  fromTokenAddress: coin.coin_address,
                  fromTokenDecimals: coin.desimal,
                  toTokenAddress: "0x0298c2b32eae4da002a15f36fdf7615bea3da047",
                  toTokenDecimals: "8",
                  slippage: 0,
                  userAddr: "0x4f2A27cd59318d20dbBE58121CeBcC0B74AddeEB",
                  chainId: "128",
                  rpc: "",
                  fromAmount: amount,
                },
                timeout: (this.$store.state.setting.settings.timeout_request || 15) * 1000,
              })
              .then(response => {
                this.running_request -= 1;
                let data = response.data.data;

                // kalkulasi selisih pnl kiri
                // let api = BigInt(data.resAmount);
                // let pembagi = BigInt("1".padEnd(19, "0"));
                // let selisih_pnl_kiri = Number((api * 100n) / pembagi) / 100 - modal_kiri_huobi_dodo_heco;
                let api = data.resAmount;
                let selisih_pnl_kiri = api - modal_kiri_huobi_dodo_heco;

                // inch_usdt
                // ( modal_kiri_huobi_dodo_heco + API_INCH_FROM ) / ( modal_kiri_huobi_dodo_heco / huobi_ask_price )
                let inch_usdt = (parseInt(modal_kiri_huobi_dodo_heco) + selisih_pnl_kiri) / (parseInt(modal_kiri_huobi_dodo_heco) / askPrice.price);

                // kalkulasi selisih_pnl_kiri_persen
                // ((inch_usdt-huobi_ask_price)/huobi_ask_price)*100
                let selisih_pnl_kiri_persen = ((inch_usdt - askPrice.price) / askPrice.price) * 100;

                this.$store.dispatch("updateSinyalHuobiDodoHeco", {
                  symbol: symbol,
                  value: {
                    selisih_pnl_kiri: selisih_pnl_kiri.toFixed(2),
                    inch_usdt: inch_usdt.toFixed(12),
                    selisih_pnl_kiri_persen: selisih_pnl_kiri_persen.toFixed(2),
                  },
                });

                let amount2 = BigInt(modal_kanan_huobi_dodo_heco * Math.pow(10, 8)).toLocaleString("fullwide", { useGrouping: false, maximumFractionDigits: 0 });
                axios
                  .get("https://route-api.dodoex.io/dodoapi/getdodoroute", {
                    cancelToken: this.axiosCancel.token,
                    params: {
                      fromTokenAddress: "0x0298c2b32eae4da002a15f36fdf7615bea3da047",
                      fromTokenDecimals: "8",
                      toTokenAddress: coin.coin_address,
                      toTokenDecimals: coin.desimal,
                      slippage: 0,
                      userAddr: "0x4f2A27cd59318d20dbBE58121CeBcC0B74AddeEB",
                      chainId: "128",
                      rpc: "",
                      fromAmount: amount2,
                    },
                    timeout: (this.$store.state.setting.settings.timeout_request || 15) * 1000,
                  })
                  .then(response => {
                    this.running_request -= 1;
                    let data2 = response.data.data;

                    // kalkulasi inch_sum
                    // let api2 = BigInt(data2.resAmount);
                    // let pembagi = BigInt("1".padEnd(parseInt(coin.desimal) + 1, "0"));
                    // let inch_sum = Number((api2 * 100n) / pembagi) / 100;
                    let api2 = data2.resAmount;
                    let inch_sum = api2;

                    // kalkulasi inch_price
                    // modal_kanan_huobi_dodo_heco/API_INCH_TO
                    let inch_price = inch_sum == 0 ? 0 : modal_kanan_huobi_dodo_heco / inch_sum;
                    console.log(api2, modal_kanan_huobi_dodo_heco, inch_price);
                    // kalkulasi selisih_pnl_kanan
                    // ((API_INCH_TO*huobi_bid_price)-modal_kanan_huobi_dodo_heco)
                    let selisih_pnl_kanan = inch_sum * bidPrice.price - modal_kanan_huobi_dodo_heco;

                    // kalkulasi selisih_pnl_kanan_persen
                    // ((huobi_bid_price-inch_price)/inch_price)*100
                    let selisih_pnl_kanan_persen = inch_sum == 0 ? 0 : ((bidPrice.price - inch_price) / inch_price) * 100;
                    if (selisih_pnl_kanan >= this.limit_threshold || selisih_pnl_kiri >= this.limit_threshold) this.threshold++;
                    let waktu = new Date();
                    this.$store.dispatch("updateSinyalHuobiDodoHeco", {
                      symbol: symbol,
                      value: {
                        inch_sum: inch_sum.toFixed(2),
                        inch_price: inch_price.toFixed(12),
                        selisih_pnl_kanan: selisih_pnl_kanan.toFixed(2),
                        selisih_pnl_kanan_persen: selisih_pnl_kanan_persen.toFixed(2),
                        waktu_fetch: ("0" + waktu.getHours()).slice(-2) + ":" + ("0" + waktu.getMinutes()).slice(-2) + ":" + ("0" + waktu.getSeconds()).slice(-2),
                      },
                    });
                  })
                  .catch(error => {
                    this.running_request -= 1;
                    let placeholder;
                    if (error.code == "ECONNABORTED") {
                      //TIMEOUT
                      placeholder = "timeout";
                      this.addLogRequest("TIMEOUT: " + symbol + " " + coin.coin_address);
                      this.axiosError.timeout.push(symbol);
                    } else if (error.response != undefined) {
                      placeholder = "error";
                      let message = error.response.data.message || "general error";
                      this.addLogRequest("ERROR: " + symbol + " : " + message + "<br>" + error.request.responseURL);
                      this.axiosError.error.push(symbol);
                    } else if (axios.isCancel(error)) {
                      this.addLogRequest("CANCELLED: " + symbol + " " + coin.coin_address);
                      placeholder = "canceled";
                      this.axiosError.error.push(symbol);
                    } else {
                      this.addLogRequest("NETWORK ERROR: " + symbol + " " + coin.coin_address + " Request ditolak oleh dodo (CORS)");
                      placeholder = "error";
                      this.axiosError.error.push(symbol);
                    }
                    // this.$bvToast.toast("Error saat menghubungi DODO untuk koin " + symbol, {
                    //   title: `API Error`,
                    //   variant: "danger",
                    //   solid: false,
                    // });
                    let waktu = new Date();
                    this.$store.dispatch("updateSinyalHuobiDodoHeco", {
                      symbol: symbol,
                      value: {
                        inch_sum: placeholder,
                        inch_price: placeholder,
                        selisih_pnl_kanan: placeholder,
                        selisih_pnl_kanan_persen: placeholder,
                        waktu_fetch: ("0" + waktu.getHours()).slice(-2) + ":" + ("0" + waktu.getMinutes()).slice(-2) + ":" + ("0" + waktu.getSeconds()).slice(-2),
                      },
                    });
                    console.log("Fetching dodo error", error);
                  });
              })
              .catch(error => {
                // console.log(error.code);
                this.running_request -= 2;
                let placeholder;
                if (error.code == "ECONNABORTED") {
                  //TIMEOUT
                  placeholder = "timeout";
                  this.addLogRequest("TIMEOUT: " + symbol + " " + coin.coin_address);
                  this.axiosError.timeout.push(symbol);
                } else if (error.response != undefined) {
                  placeholder = "error";
                  let message = error.response.data.message || "general error";
                  this.addLogRequest("ERROR: " + symbol + " : " + message + " \n " + error.request.responseURL + " \n Cek nama koin!");
                  this.axiosError.error.push(symbol);
                } else if (axios.isCancel(error)) {
                  this.addLogRequest("CANCELLED: " + symbol + " " + coin.coin_address);
                  placeholder = "canceled";
                  this.axiosError.error.push(symbol);
                } else {
                  this.addLogRequest("NETWORK ERROR: " + symbol + " " + coin.coin_address + " Request ditolak oleh dodo (CORS)");
                  placeholder = "error";
                  this.axiosError.error.push(symbol);
                }
                // this.$bvToast.toast("Error saat menghubungi DODO untuk koin " + symbol + ":\n" + error.response.data.message, {
                //   title: `Error dodo`,
                //   variant: "danger",
                //   solid: false,
                // });
                let waktu = new Date();
                this.$store.dispatch("updateSinyalHuobiDodoHeco", {
                  symbol: symbol,
                  value: {
                    selisih_pnl_kiri: placeholder,
                    inch_usdt: placeholder,
                    selisih_pnl_kiri_persen: placeholder,
                    inch_sum: placeholder,
                    inch_price: placeholder,
                    selisih_pnl_kanan: placeholder,
                    selisih_pnl_kanan_persen: placeholder,
                    waktu_fetch: ("0" + waktu.getHours()).slice(-2) + ":" + ("0" + waktu.getMinutes()).slice(-2) + ":" + ("0" + waktu.getSeconds()).slice(-2),
                  },
                });
                console.log("Fetching dodo error", error);
              });
          }); // dispatch updateSinyalHuobiDodoHeco
      }, rehat_huobi_dodo_heco + penambah);
    },
    bintangFind(koin) {
      const idx = this.list_koin_bintang_huobi_dodo_heco.findIndex(x => x.koin == koin);
      if (idx >= 0) {
        return this.list_koin_bintang_huobi_dodo_heco[idx].class;
      } else {
        return "bintang-white";
      }
    },
    bintangMark(event, koin) {
      console.log(koin);
      const idx = this.list_koin_bintang_huobi_dodo_heco.findIndex(x => x.koin == koin);
      let newclass = "";
      if (idx >= 0) {
        const old = this.list_koin_bintang_huobi_dodo_heco.splice(idx, 1);
        if (old[0].class == "bintang-white") {
          newclass = "bintang-green";
        } else if (old[0].class == "bintang-green") {
          newclass = "bintang-red";
        } else {
          newclass = "bintang-white";
        }
        this.list_koin_bintang_huobi_dodo_heco.push({
          koin,
          class: newclass,
        });
      } else {
        newclass = "bintang-green";
        this.list_koin_bintang_huobi_dodo_heco.push({
          koin,
          class: newclass,
        });
      }

      localStorage.setItem("mw_koin_bintang_huobi_dodo_heco", JSON.stringify(this.list_koin_bintang_huobi_dodo_heco));

      if (newclass == "bintang-white") {
        event.target.classList.add("bintang-white");
        event.target.classList.remove("bintang-red");
        event.target.classList.remove("bintang-green");
      } else if (newclass == "bintang-green") {
        event.target.classList.remove("bintang-white");
        event.target.classList.remove("bintang-red");
        event.target.classList.add("bintang-green");
      } else {
        event.target.classList.remove("bintang-white");
        event.target.classList.add("bintang-red");
        event.target.classList.remove("bintang-green");
      }
    },
    CopyToClipboard(containerid) {
      console.log(containerid);
      let selrange = document.createRange();
      selrange.selectNode(document.getElementById(containerid));
      window.getSelection().removeAllRanges(); // clear current selection
      window.getSelection().addRange(selrange); // to select text
      document.execCommand("copy");
      window.getSelection().removeAllRanges(); // to deselect
      this.$bvToast.toast("data dicopy", {
        title: `Clipboard`,
        variant: "info",
        solid: false,
      });
    },
    kalkulatorTool(pair, harga) {
      this.kalkulator_data.pair = pair;
      this.kalkulator_data.harga = harga;
      this.kalkulator_data.trigger = !this.kalkulator_data.trigger;
      this.$refs["modal-calculator"].show();
    },
  },
};
</script>
